import { Component, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { TestService } from '../test.service';

@Component({
  selector: 'app-tab5',
  templateUrl: './tab5.page.html',
  styleUrls: ['./tab5.page.scss'],
})
export class Tab5Page implements OnInit {
  ultimasdiez 
  ultimasdiezHumo
  ultimamedicionHumo
  ultimamedicion
  ambiente
  showMessage = false
  alarmaActiva
  constructor(private testService : TestService, private router: Router) {
    router.events.subscribe((val) => {
      if(val instanceof NavigationEnd){
    this.ambiente = localStorage.getItem('ambienteseleccionado');
        
        if(this.ambiente == 'cocina'){
          this.getData()
          this.showMessage = false

        }
        else{
          this.showMessage = true
        }

      }
  });
}
  ngOnInit(): void {
    // this.ultimasdiez = JSON.parse(this.getData2())
    // console.log(this.ultimasdiez)
    this.ambiente = localStorage.getItem('ambienteseleccionado');

    if(this.ambiente == 'cocina'){
      console.log("es cocina")
      this.getData()
    }
    else{

      this.showMessage = true
      
    }
  }

  getData(){
    this.testService.interiorAlarma().subscribe(
      res => {
        console.log(res)
        this.ultimasdiez = res
        this.alarmaActiva = res[0]
      },
      error => {
        console.log(error)
      }
    )

  }
  getHumo(){
    this.testService.humoCocina().subscribe(
      res => {
      
        this.ultimasdiezHumo = res
        this.ultimamedicionHumo = this.ultimasdiezHumo[0]
        console.log(this.ultimamedicionHumo.value)
    // this.drawGaugeHumo(this.ultimamedicionHumo.value)

      },
      error => {
        console.log(error)
      }
    )

  }
  getMonoxido(){
    this.testService.monoxidoCocina().subscribe(
      res => {
        console.log(res)
        this.ultimasdiez = res
        this.ultimamedicion = this.ultimasdiez[8]
        console.log(this.ultimamedicion.value)
    // this.drawGauge(this.ultimamedicion.value)

      },
      error => {
        console.log(error)
      }
    )

  }
  apagarAlarma(){
    this.testService.apagarAlarma().subscribe(
      res => {
        console.log(res)
        this.getData()
      }
    )
  }
}